import React, { useState } from "react";
import { generateProof } from "../utils/generateProof";

const Register = () => {
    const [employeeId, setEmployeeId] = useState("");
    const [biometricId, setBiometricId] = useState("");
    const [proofData, setProofData] = useState(null);

    const handleRegister = async () => {
        if (!employeeId && !biometricId) {
            alert("Enter either Employee ID or Biometric ID!");
            return;
        }

        const userId = employeeId || biometricId;
        const hashedId = await sha256(userId);
        const did = `did:pramaan:${hashedId.slice(0, 16)}`;

        if (localStorage.getItem(userId)) {
            alert("Already registered!");
            return;
        }

        // Generate Proof
        const proof = await generateProof(hashedId);
        setProofData(proof);

        localStorage.setItem(userId, JSON.stringify({ did, proof }));
        alert(" Successful Registration! Please download your proof.");
    };

    const downloadProof = () => {
        const element = document.createElement("a");
        const file = new Blob([JSON.stringify(proofData, null, 2)], { type: "application/json" });
        element.href = URL.createObjectURL(file);
        element.download = "proof.json";
        document.body.appendChild(element);
        element.click();
    };

    return (
        <div className="container">
            <h2>Register</h2>
            <input type="text" placeholder="Enter Employee ID" onChange={(e) => setEmployeeId(e.target.value)} />
            <input type="text" placeholder="Enter Biometric ID" onChange={(e) => setBiometricId(e.target.value)} />
            <button onClick={handleRegister}>Register</button>
            {proofData && <button onClick={downloadProof}>Download Proof</button>}
        </div>
    );
};

const sha256 = async (message) => {
    const msgBuffer = new TextEncoder().encode(message);
    const hashBuffer = await crypto.subtle.digest("SHA-256", msgBuffer);
    return Array.from(new Uint8Array(hashBuffer)).map(b => b.toString(16).padStart(2, "0")).join("");
};

export default Register;
